@import "../../global.scss";

.topbar{ 
    width: 100%;
    height: 70px;
    background-color: white;
    color: black;
    position: fixed;
    top:0;
    z-index: 3;
    transition: all 2s ease;


    .wrapper{ 

        padding: 10px 30px;
        display:flex;
        align-items: center;
        justify-content: space-between;

        .left{ 
            display: flex;
            align-items: center;
            .logo{ 
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }
            .itemContainer{ 
                display: flex;
                align-items: center;
                margin-left: 30px;

                .icon{ 
                font-size: 18;
                margin-right: 5px;
                }
                span{ 
                    font-size: 15;
                    font-weight: 400   ;
                }
            }
        }
        .right{ 
            
            .hamburger{ 
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                span{ 
                    width: 100%;
                    height: 3px;
                    background-color: black;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active{ 
      background-color: black;
        color: white;

        .hamburger{ 
            span{ 
                &:first-child{ 
                    background-color: white;
                    transform: rotate(44.9deg);
                }
                &:nth-child(2){ 
                    opacity: 0;
                }
                &:last-child{ 
                    background-color: white;
                    transform: rotate(-44.9deg);
                }
            }
        }
    }
}